"use client";

import { useEffect, useState } from "react";
import Image from "next/image";
import Link from "next/link";
import { RxHamburgerMenu } from "react-icons/rx";

export default function Header({ onMenuClick }) {
  const [showMainHeader, setShowMainHeader] = useState(true);
  const [scrollY, setScrollY] = useState(0);

  useEffect(() => {
    const handleScroll = () => {
      const currentY = window.scrollY;
      setShowMainHeader(currentY < scrollY || currentY < 50);
      setScrollY(currentY);
    };

    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, [scrollY]);

  return (
    <>
      <div
        id="headicons"
        className={`position-fixed top-0 start-0 end-0 z-3 transition-transform`}
        style={{
          transform: showMainHeader ? "translateY(0)" : "translateY(-100%)",
          transition: "transform 0.3s ease",
          width: "75%",
          margin: "auto",
        }}
      >
        <div className="d-flex justify-content-between align-items-center px-4 py-2 bg-white shadow">
          <Link href="/">
            <Image
              src="/images/PinkMirror-Favicon.png"
              alt="PinkMirror Logo"
              width={70}
              height={70}
              style={{ margin: "11px 0" }}
            />
          </Link>

          <div
            className="rounded-circle p-2 mx-2 d-flex align-items-center justify-content-center"
            style={{
              marginTop: "10px",
              backgroundColor: "rgb(247, 148, 29)",
              boxShadow: "0 0 10px rgba(0,0,0,0.15)",
              width: "40px",
              height: "40px",
              cursor: "pointer",
            }}
            onClick={onMenuClick}
          >
            <RxHamburgerMenu color="#fff" />
          </div>
        </div>
      </div>

      {!showMainHeader && (
        <div
          className="position-fixed top-0 start-0 end-0 z-2 bg-warning text-white px-4 py-2 shadow"
          style={{ transition: "opacity 0.3s ease" }}
        >
          <div className="d-flex justify-content-between align-items-center">
            <span className="fw-semibold fs-5">PinkMirror</span>
            <RxHamburgerMenu size={24} onClick={onMenuClick} />
          </div>
        </div>
      )}
    </>
  );
}
